/* global jQuery Swiper */

(function ($) {
	"use strict";

	function initImgsSwiper(el) {
		return new Swiper(el, {
			loop: true,
			autoplay: {
				delay: 3000,
				disableOnInteraction: false,
			},
			observer: true,
			observeParents: true
		});
	}

	var tab = $(".js-txt-header");

	function activateAccordion($this) {
		var $accordion = $this.parents(".js-accordion-items");
		var $screnshots = $accordion.next(".q-accordion-images");
		var $imageData = $this.data("image");

		if ($accordion.hasClass("accordion-init")) {
			$accordion.removeClass("accordion-init");
		}

		// Close all accordions and hide their images
		$this.parents(".q-accordion-items").find(".q-accordion-item").removeClass("active");
		$this.parents(".q-accordion-items").find(".js-txt-content").slideUp(350);
		$screnshots.find(".js-img-screenshot").hide(0, function () {
			$("." + $imageData).show(0, function () {
				var imgEl = $("." + $imageData + " > .swiper-imgs");
				const imgSwipe = initImgsSwiper(imgEl[0]);
				imgSwipe.update();
			});
		});

		// Open the clicked accordion
		$this.parents(".q-accordion-item").addClass("active");
		$this.next().slideDown(350);
	}

	if (tab.length && !tab.parents(".q-accordion-item").hasClass("active")) {
		tab.click(function () {
			activateAccordion($(this));
		});
	}

	// Initialize Swiper for the first slide
	initImgsSwiper($(".swiper-imgs")[0]);

})(jQuery);
